<template>
	<view class="flex flex-direction bg-white">
		<!-- 判断是否登录 -->
		<is-login  currentPage="index"></is-login>
		<!-- 导航栏 -->
		<cu-custom bgColor="bg-cyan" :isBack="preview">
			<block slot="content">产品型号</block>
		</cu-custom>
		<luyj-search-bar cancelColor="#999999" radius="80" placeholder="请输入产品型号" @input="search_input" @cancel="search_cancel"></luyj-search-bar>
		<view class="padding solid text-grey">
			电子设备>打印机
		</view>
		<uni-list>
			<uni-list-item v-for="(item,index) in model_list" :key="index" :title="item.Name" :note="item.CategoryLink" clickable @click="editProductModel(item.ModelId)" >
			<template slot="footer" @tap.stop="">
				<view class="cu-tag margin-top round bg-cyan" @click="editProductModel(item.ModelId)" ><text class="cuIcon-edit"></text></view>
				<view class="cu-tag margin-top round bg-red" @click="deleteProductModel" :data-id="item.ModelId"><text class="cuIcon-delete"></text></view>
				<navigator :url="'/pages/basic/product/product/home?preview=true&ModelId=' + item.ModelId" class="cu-tag bg-cyan light round margin-top">设备</navigator>
			</template>
			</uni-list-item>
		</uni-list>
		<!-- 弹出悬浮框 -->
		<uni-fab ref="fab" :pattern="pattern"  horizontal="left" vertical="bottom"
			direction="horizontal" @fabClick="addproductModel" />
		<!-- 编辑产品型号弹出框 -->
		<view v-if="editModalShow">
			<model-add :MchId="MchId" :Token="Token" :ModelId="currentModelId" :CategoryId="CategoryId" :isEdit="isEdit"></model-add>
		</view>
	</view>
</template>

<script>
	/**
	 * 产品型号
	 * @author 陆彦捷
	 * @date 2021.06.30 产品型号的样式
	 * @date 2021.07.23 重新引用login
	 * @description 产品型号的基本操作
	 */
	import organ from '@/common/storage/organstorage.js';	// 组织
	
	import models from '@/common/data/model.js';	// 产品型号
	var _self;
	export default {
		data() {
			return {
				// 页面样式参数
				preview : false,			// 上级页面
				pattern: {
					color: '#7A7E83', // 文字默认颜色
					selectedColor: '#00BEB7',
					backgroundColor: '#fff',
					buttonColor: '#00BEB7'
				}, // 可选配置项样式（悬浮按钮）
				content: [{
						iconPath: '/static/fabIcon/add.png',
						selectedIconPath: '/static/fabIcon/addHL.png',
						text: '添加',
						active: true
					},
					{
						iconPath: '/static/fabIcon/edit.png',
						selectedIconPath: '/static/fabIcon/editHL.png',
						text: '编辑',
						active: false
					},
					{
						iconPath: '/static/fabIcon/delete.png',
						selectedIconPath: '/static/fabIcon/deleteHL.png',
						text: '删除',
						active: false
					},
				], // 展开菜单配置项
				editModalShow : false,		// Modal是否显示
				isEdit: false,				// 是否编辑样式
				// 系统参数
				CategoryId : 0 ,			// 产品分类id
				MchId : 0,
				Token : this.global.login.getToken(),	// 当前Token
				model_list : [],			//	产品型号
				model_list_search :[],		// 当前搜索到的产品型号
				currentModelId : 0 ,		// 当前产品型号
			}
		},
		onLoad:function(options){
			_self = this;
			this.getParams(options);		// 获取当期参数
			this.models_request();			// 产品型号请求
		},
		methods: {
			// ===================================== 初始化 ============================================================
			/** 获取当期参数
			 * @param {Object} options
			 */
			getParams : function(options){
				this.preview = options.preview != 'false' ? Boolean(options.preview): false;		// 是否返回上级页面
				this.CategoryId = options.CategoryId ? options.CategoryId : 0;	// 产品分类id
			},
			// ===================================== 监听事件 ===========================================================
			/** 搜索值改变时执行事件
			 * @param {Object} e
			 */
			search_input : function(e){
				console.log("搜索值改变是执行=>",e);
			},
			/** 取消搜索时执行事件
			 * @param {Object} e
			 */
			search_cancel : function(e){
				console.log("取消搜索时执行=>",e);
			},
			/** 添加产品型号
			 * @param {Object} e
			 */
			addproductModel : function(e){
				this.isEdit = false;
				this.reloadcompent();		// 重新加载弹出框
			},
			/** 编辑产品型号
			 * @param {Object} e
			 */
			editProductModel : function(ModelId){
				this.isEdit = true;
				this.currentModelId = ModelId;
				this.reloadcompent();		// 重新加载弹出框
			},
			/** 删除产品型号
			 * @param {Object} e
			 */
			deleteProductModel : function(e){
				this.currentModelId = e.currentTarget.dataset.id;
				this.modeldelete(this.currentModelId);
			},
			// ===================================== 公共事件 ==========================================================
			/**
			 * 重新渲染弹出
			 */
			reloadcompent() {
				_self.editModalShow = false;
				_self.$nextTick(() => {
					_self.editModalShow = true;
				});
			},
			/** 删除产品型号
			 * @param {Object} ModelId
			 */
			modeldelete : function(ModelId){
				uni.showModal({
					title:'删除',
					content:'是否删除当前产品型号？',
					success:function(result){
						if(result.confirm){
							_self.modeldelete_request(ModelId);
						}
					}
				})
			},
			// ===================================== 请求事件 ===========================================================
			/**
			 * 搜索产品型号
			 */
			models_request : function(){
				uni.showLoading();
				setTimeout(function(){
					uni.hideLoading();
					_self.model_list = models.list;
				},3000);
				
			},
			/** 删除当前产品型号
			 * @param {Object} ModelId 产品型号id
			 */
			modeldelete_request : function(ModelId){
				uni.showLoading();
				setTimeout(function(){
					uni.hideLoading();
					uni.showToast({
						title:'假装删除' + ModelId + '成功！'
					});
					// _self.models_request();
				} ,3000);
			}
			// ==========================================================================================================
		}
	}
</script>

